.home {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  row-gap: 6rem;
}

.carousel {
  width: 100%;
}

.carousel-image {
  height: 60rem;
  position: relative;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: block;
  background: rgba(0, 0, 0, 0.6);
}
.carousel-caption {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  row-gap: 2rem;
  top: 40%;
  bottom: auto;
}

.carousel-caption-title {
  font-family: var(--font-primary);
  font-size: var(--font-large);
}

.carousel-caption-text {
  font-family: var(--font-primary);
  font-size: var(--font-medium);
  line-height: 1.5;
}

.home-mid {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.mid-row {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.mid-row:nth-child(even) {
  flex-direction: row-reverse;
}

.mid-row-text {
  display: block;
  width: 50%;
  text-align: center;
  font-family: var(--font-primary);
  font-size: var(--font-small);
  color: var(--font-color-primary);
  line-height: 1.5;
  padding: 1rem 3rem;
  margin: 0;
}

.mid-row-logo-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50%;
}

.mid-row-logo {
  display: block;
  width: 40%;
  height: auto;
}

.mid-row-image-container {
  position: relative;
  display: block;
  width: 50%;
}

.mid-row-image {
  width: 100%;
  object-fit: cover;
  z-index: 10;
}

.mid-row-image-hover {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.6);
  z-index: 1;
  opacity: 0;
  transition: all 0.2s;
}

.mid-row-image-container:hover .mid-row-image-hover {
  z-index: 20;
  opacity: 1;
}

.mid-row-link,
.mid-row-link:active,
.mid-row-link:visited {
  text-decoration: none;
  font-family: var(--font-primary);
  font-size: var(--font-large);
  color: aliceblue;
  padding-bottom: 0;
  border-bottom: none;
  transition: all 0.2s;
}

.mid-row-link:hover {
  padding-bottom: 1rem;
  border-bottom: 1px solid aliceblue;
}

@media only screen and (max-width: 1000px) {
  .carousel-caption-title {
    font-size: var(--font-medium);
  }

  .carousel-caption-text {
    font-size: var(--font-extra-small);
    line-height: 1.5;
  }

  .mid-row-text {
    font-size: var(--font-extra-small);
    line-height: 1.5;
    padding: 1rem 2rem;
  }
}

@media only screen and (max-width: 800px) {
  .mid-row-text {
    font-size: var(--font-mini);
    line-height: 1.5;
  }

  .mid-row-link,
  .mid-row-link:active,
  .mid-row-link:visited {
    font-size: var(--font-medium);
  }
}

@media only screen and (max-width: 600px) {
  .carousel-caption {
    top: 35%;
    bottom: auto;
  }

  .carousel-caption-title {
    font-size: var(--font-small);
  }

  .carousel-caption-text {
    font-size: var(--font-extra-small);
  }

  .mid-row-text {
    font-size: 1.1rem;
  }
}

@media only screen and (max-width: 500px) {
  .home {
    row-gap: 4rem;
  }
  .carousel {
    height: 30rem;
  }

  .carousel-image {
    height: 30rem;
  }

  .carousel-caption {
    top: 30%;
    bottom: auto;
  }
  .carousel-caption-title {
    font-size: var(--font-extra-small);
  }

  .carousel-caption-text {
    font-size: 1.5rem;
  }

  .home-mid {
    row-gap: 3rem;
  }

  .mid-row {
    flex-direction: column;
    row-gap: 2rem;
  }

  .mid-row:nth-child(even) {
    flex-direction: column;
  }

  .mid-row-text {
    display: block;
    width: 90%;
    font-size: 1.5rem;
    padding: 1rem 1rem;
  }

  .mid-row-logo-container {
    display: none;
  }
  .mid-row-image-container {
    width: 90%;
  }
}

@media only screen and (max-width: 450px) {
  .carousel-caption-title {
    font-size: 1.5rem;
  }

  .carousel-caption-text {
    font-size: 1.1rem;
  }
}
